import * as React from "react";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";

const Cart = () => {
  const meals = useSelector((state) => state.meals);
  const orderPrice = useSelector((state) => state.orderPrice);
  const dispatch = useDispatch();
  const [renderFlag, setRenderFlag] = React.useState(false);

  const placeOrder = () => {
    if (meals && meals.length > 0) {
      const config = {
        headers: {
          "x-access-token": localStorage.getItem("token"),
          type: localStorage.getItem("type"),
        },
      };
      const tempOrder = {
        meals: meals,
        sellerID: meals[0].sellerID,
        restID: meals[0].restID,
        userID: localStorage.getItem("ID"),
        price: orderPrice,
      };
      axios.post("order/place-order", tempOrder, config).then((res) => {
        console.log(res);
        setRenderFlag(!renderFlag);
        dispatch({
          type: "renderFlag",
          payload: { message: orderPrice },
        });
      });
    }
  };

  const renderCartMeals = () => {
    if (meals && meals.length > 0) {
      return (
        <div>
          {meals.map((item) => (
            <div style={{ display: "flex" }}>
              <Box
                key={item._id}
                component="span"
                sx={{
                  display: "block",
                  width: "fit-content",
                  cursor: "pointer",
                  p: 1,
                  m: 1,
                  bgcolor: (theme) =>
                    theme.palette.mode === "dark" ? "#101010" : "#fff",
                  color: (theme) =>
                    theme.palette.mode === "dark" ? "grey.300" : "grey.800",
                  border: "1px solid",
                  borderColor: (theme) =>
                    theme.palette.mode === "dark" ? "grey.800" : "grey.300",
                  borderRadius: 2,
                  fontSize: "0.875rem",
                  fontWeight: "700",
                }}
              >
                {item.name} (Rs. {item.price})
              </Box>
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <>
          <h3>Let's Add Something</h3>
        </>
      );
    }
  };

  return (
    <>
      <div>
        <Box sx={{ minWidth: 275 }}>
          <Card variant="outlined">
            <React.Fragment>
              <CardContent>
                <Typography
                  sx={{ fontSize: 14 }}
                  color="text.secondary"
                  gutterBottom
                >
                  Enjoy your Meal
                </Typography>
                <Typography variant="h5" component="div">
                  Cart
                </Typography>
                {renderCartMeals()}
              </CardContent>
              <CardActions>
                Rs. {orderPrice}
                <Button
                  style={{ marginLeft: "6pc" }}
                  size="small"
                  variant="contained"
                  onClick={() => {
                    placeOrder();
                    dispatch({
                      type: "setMeals",
                      payload: { meals: [] },
                    });
                    // window.location.reload();
                  }}
                >
                  Place Order
                </Button>
              </CardActions>
            </React.Fragment>
          </Card>
        </Box>
      </div>
    </>
  );
};

export default Cart;
