import {
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import { useState } from "react";
import axios from "axios";
import Parent from "./parent";
import { useNavigate } from "react-router-dom";

const AddRestaurant = () => {
  let navigate = useNavigate();
  const [name, setName] = useState("");
  const [type, setType] = useState("");
  const [desc, setDesc] = useState("");
  const [button_info, setButtonInfo] = useState({
    style: { background: "default" },
    text: "Add",
  });

  const onAdd = () => {
    const temp = {
      name: name,
      type: type,
      sellerID: localStorage.getItem("ID"),
      description: desc,
    };
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios.post("/restaurants", temp, config).then((res) => {
      if (res.data.status === "success") {
        setButtonInfo({
          style: { background: "green" },
          text: "Added",
        });
      } else {
        setButtonInfo({
          style: { background: "red" },
          text: "Error",
        });
      }
      setTimeout(function () {
        navigate("/seller-home/restaurants");
      }, 1000);
    });
  };

  return (
    <Parent>
      <div style={{ color: "black", marginLeft: "20vw" }}>
        <h3>Add Restaurant</h3>
        <div>
          <TextField
            onChange={(e) => {
              setName(e.target.value);
            }}
            label="Name"
            variant="standard"
          />
        </div>
        <div>
          <FormControl variant="standard" sx={{ minWidth: 200 }}>
            <InputLabel id="demo-simple-select-standard-label">Type</InputLabel>
            <Select
              labelId="demo-simple-select-standard-label"
              id="demo-simple-select-standard"
              value={type}
              onChange={(e) => {
                setType(e.target.value);
              }}
              label="Type"
            >
              <MenuItem value={"desi"}>Desi</MenuItem>
              <MenuItem value={"bbq"}>BBQ</MenuItem>
              <MenuItem value={"fast-food"}>Fast Food</MenuItem>
            </Select>
          </FormControl>
        </div>
        <div>
          <TextField
            onChange={(e) => {
              setDesc(e.target.value);
            }}
            label="Description"
            variant="standard"
          />
        </div>
        <div style={{ marginLeft: "4pc", marginTop: "1pc" }}>
          <Button
            style={button_info.style}
            onClick={() => {
              onAdd();
              setName("");
              setType("");
              setDesc("");
              setButtonInfo({
                style: { background: "default" },
                text: "Add",
              });
            }}
            variant="contained"
          >
            {button_info.text}
          </Button>
        </div>
      </div>
    </Parent>
  );
};

export default AddRestaurant;
