import * as React from "react";
import {
  HomeRounded,
  LogoutRounded,
  RestaurantRounded,
  DeliveryDiningRounded,
} from "@mui/icons-material";
import {
  Box,
  Drawer,
  AppBar,
  CssBaseline,
  Toolbar,
  List,
  Typography,
  Divider,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router";

const drawerWidth = 240;

export default function UserSidebar() {
  const dispatch = useDispatch();
  let navigate = useNavigate();
  const [title, setTitle] = useState(localStorage.getItem("name"));

  const dispatchComponentName = (name) => {
    dispatch({
      type: "setRenderComponent",
      payload: { renderComponent: name },
    });
  };

  useEffect(() => {
    setTitle(localStorage.getItem("name"));
  }, []);

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}
      >
        <Toolbar>
          <Typography variant="h6" noWrap component="div">
            Hello {title.charAt(0).toUpperCase() + title.slice(1)}
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          [`& .MuiDrawer-paper`]: {
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
      >
        <Toolbar />
        <Box sx={{ overflow: "auto" }}>
          <List>
            <ListItem
              button
              onClick={() => {
                navigate("/customer-home");
                dispatchComponentName("home");
              }}
            >
              <ListItemIcon>
                <HomeRounded></HomeRounded>
              </ListItemIcon>
              <ListItemText primary="Home" />
            </ListItem>

            <ListItem
              button
              onClick={() => {
                navigate("/view-restaurants");
                dispatchComponentName("view all restaurants");
              }}
            >
              <ListItemIcon>
                <RestaurantRounded></RestaurantRounded>
              </ListItemIcon>
              <ListItemText primary="View Restaurants" />
            </ListItem>

            <ListItem
              button
              onClick={() => {
                navigate("/view-orders");
                dispatchComponentName("view user orders");
              }}
            >
              <ListItemIcon>
                <DeliveryDiningRounded></DeliveryDiningRounded>
              </ListItemIcon>
              <ListItemText primary="View Orders" />
            </ListItem>
          </List>
          <Divider />
          <List style={{ background: "#cce4f0", borderRadius: "10px" }}>
            <ListItem
              button
              onClick={() => {
                setTimeout(() => {
                  localStorage.clear();
                  navigate("/");
                }, 1000);
                dispatchComponentName("logout");
              }}
            >
              <ListItemIcon>
                <LogoutRounded></LogoutRounded>
              </ListItemIcon>
              <ListItemText style={{ paddingLeft: "13px" }} primary="Log Out" />
            </ListItem>
          </List>
        </Box>
      </Drawer>
      <Box component="main" sx={{ flexGrow: 1, p: 0 }}>
        <Toolbar />
      </Box>
    </Box>
  );
}
