import React from "react";
import { useState } from "react";
import "../styles/login-signup.scss";
import axios from "axios";
import * as EmailValidator from "email-validator";
import { useNavigate } from "react-router-dom";
import { Modal, Box, Typography } from "@mui/material";

const LoginComponent = () => {
  const [mode, setMode] = useState("login");
  const [open, setOpen] = useState(false);
  const [error, setError] = useState("");
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const navigate = useNavigate();
  const [showError, setShowError] = useState(false);

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    color: "black",
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const renderError = (message) => {
    if (showError) {
      return (
        <div>
          <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
          >
            <Box sx={style}>
              <Typography id="modal-modal-title" variant="h6" component="h2">
                {message}
              </Typography>
            </Box>
          </Modal>
        </div>
      );
    }
  };

  const validateSignup = (info) => {
    const types = ["seller", "customer"];
    if (
      info.name.length > 0 &&
      EmailValidator.validate(info.email) &&
      info.password.length >= 8 &&
      info.type.length > 0 &&
      types.includes(info.type)
    ) {
      return true;
    } else {
      return false;
    }
  };

  const onLogin = (info) => {
    const tempUser = {
      email: info.email,
      password: info.password,
    };
    axios.post("/login", tempUser).then((res) => {
      if (res.data.message === "success") {
        setShowError(false);
        if (res.data.type === "seller") {
          localStorage.setItem("email", info.email);
          localStorage.setItem("type", "seller");
          localStorage.setItem("name", res.data.name);
          localStorage.setItem("ID", res.data.id);
          localStorage.setItem("token", res.data.token);
          navigate("seller-home");
        } else {
          localStorage.setItem("email", info.email);
          localStorage.setItem("type", "customer");
          localStorage.setItem("name", res.data.name);
          localStorage.setItem("ID", res.data.id);
          localStorage.setItem("token", res.data.token);
          navigate("customer-home");
        }
      } else {
        setError("Invalid Credentials. Please Try Again.");
        handleOpen();
        setShowError(true);
      }
    });
  };

  const onSigup = (info) => {
    const tempUser = {
      name: info.name,
      email: info.email,
      password: info.password,
      type: info.type,
    };
    const isValid = validateSignup(info);

    if (isValid) {
      axios.post("/signup", tempUser).then((res) => {
        console.log(res);
        if (res.data.status === "success") {
          setShowError(false);
          window.location.reload();
        } else {
          handleOpen();
          setShowError(true);
          setError("Some Error Occured. Please Try Again");
        }
      });
    } else {
      handleOpen();
      setShowError(true);
      setError("Please Fill All the Fields Correctly");
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (mode === "signup") {
      onSigup({
        name: e.target.fullname.value,
        email: e.target.email.value,
        password: e.target.createpassword.value,
        type: e.target.type.value,
      });
    } else {
      onLogin({
        email: e.target.username.value,
        password: e.target.password.value,
      });
    }
  };

  const toggleMode = () => {
    var newMode = mode === "login" ? "signup" : "login";
    setMode(newMode);
  };

  return (
    <div>
      {renderError(error)}
      <div
        className={`form-block-wrapper form-block-wrapper--is-${mode}`}
      ></div>
      <section className={`form-block form-block--is-${mode}`}>
        <header className="form-block__header">
          <h1>{mode === "login" ? "Welcome back!" : "Sign up"}</h1>
          <div className="form-block__toggle-block">
            <span>
              {mode === "login" ? "Don't" : "Already"} have an account? Click
              here &#8594;
            </span>
            <input id="form-toggler" type="checkbox" onClick={toggleMode} />
            <label htmlFor="form-toggler"></label>
          </div>
        </header>
        <LoginForm mode={mode} onSubmit={handleSubmit} />
      </section>
    </div>
  );
};

const LoginForm = (props) => {
  const [passColor, setPassColor] = useState("red");
  const [emailColor, setEmailColor] = useState("red");
  const [typeColor, setTypeColor] = useState("red");
  const [nameColor, setNameColor] = useState("red");

  return (
    <form onSubmit={props.onSubmit}>
      <div className="form-block__input-wrapper">
        <div className="form-group form-group--login">
          <Input
            type="text"
            id="username"
            label="user name"
            disabled={props.mode === "signup"}
          />
          <Input
            type="password"
            id="password"
            label="password"
            disabled={props.mode === "signup"}
          />
        </div>
        <div className="form-group form-group--signup">
          <Input
            type="text"
            id="fullname"
            label="full name"
            style={{ color: nameColor }}
            onChange={(e) => {
              const temp = /^[a-z A-Z()]+$/.test(e.target.value);
              if (temp && e.target.value.length > 0) {
                setNameColor("white");
              } else {
                setNameColor("red");
              }
            }}
            disabled={props.mode === "login"}
          />
          <Input
            type="email"
            id="email"
            label="email"
            style={{ color: emailColor }}
            onChange={(e) => {
              if (EmailValidator.validate(e.target.value)) {
                setEmailColor("white");
              } else {
                setEmailColor("red");
              }
            }}
            disabled={props.mode === "login"}
          />
          <Input
            type="password"
            id="createpassword"
            label="password"
            style={{ color: passColor }}
            onChange={(e) => {
              if (e.target.value.length >= 8) {
                setPassColor("white");
              } else {
                setPassColor("red");
              }
            }}
            disabled={props.mode === "login"}
          />
          <Input
            style={{ color: typeColor }}
            id="type"
            onChange={(e) => {
              const temp = ["seller", "customer"];
              if (temp.includes(e.target.value)) {
                setTypeColor("white");
              } else {
                setTypeColor("red");
              }
            }}
            label="type"
            disabled={props.mode === "login"}
          />
        </div>
      </div>
      <button className="button button--primary full-width" type="submit">
        {props.mode === "login" ? "Log In" : "Sign Up"}
      </button>
    </form>
  );
};

const Input = ({ style, id, type, label, disabled, onChange }) => (
  <input
    className="form-group__input"
    type={type}
    style={style}
    onChange={onChange}
    id={id}
    placeholder={label}
    disabled={disabled}
  />
);

export default LoginComponent;
