export const setRenderComponent = (state, name) => {
  return {
    ...state,
    renderComponent: name,
  };
};

export const setRestaurants = (state, list) => {
  return {
    ...state,
    restaurants: list,
  };
};

export const setOrders = (state, list) => {
  return {
    ...state,
    orders: list,
  };
};

const calculatePrice = (meals) => {
  var tempPrice = 0;
  for (var i = 0; i < meals.length; i++) {
    tempPrice = tempPrice + meals[i].price;
  }
  return tempPrice;
};

export const setMeals = (state, list) => {
  return {
    ...state,
    meals: list,
    orderPrice: calculatePrice(list),
  };
};

