import { useEffect, useState } from "react";
import UserSidebar from "./sidebar";
import { useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import ViewOrders from "./view-orders";
import ViewRestaurants from "./view-restaurants";
import ViewRestaurantDetails from "./view-meals";
import axios from "axios";

const CustomerHome = () => {
  let navigate = useNavigate();
  const selectState = useSelector((state) => state);
  const [restaurantFlag, setRestaurantFlag] = useState(false);
  const [orderFlag, setOrderFlag] = useState(false);
  const dispatch = useDispatch();

  const renderComponent = () => {
    if (selectState.renderComponent === "home") {
      return (
        <div>
          <h2>Hello !!!</h2>
        </div>
      );
    } else if (selectState.renderComponent === "view all restaurants") {
      if (!restaurantFlag) {
        setRestaurantFlag(true);
      }
      // navigate("/view-restaurants");
      // return <ViewRestaurants></ViewRestaurants>;
    } else if (selectState.renderComponent === "view user orders") {
      if (!orderFlag) {
        setOrderFlag(true);
      }
      // return <ViewOrders></ViewOrders>;
    } else if (selectState.renderComponent === "view restaurant details") {
      return (
        <div>
          <ViewRestaurantDetails></ViewRestaurantDetails>
        </div>
      );
    } else if (selectState.renderComponent === "logout") {
      setTimeout(() => {
        localStorage.clear();
        window.location.href = "http://localhost:3000/";
      }, 1000);
    }
  };

  return (
    <>
      <div>
        <UserSidebar></UserSidebar>
      </div>
      <div style={{ color: "black", marginLeft: "20vw" }}>
        <div>
          <h2>Hello !!!</h2>
        </div>
      </div>
    </>
  );
};

export default CustomerHome;
