import * as actions from "./actionType";
import {
  setMeals,
  setOrders,
  setRenderComponent,
  setRestaurants,
} from "./action";

const initialState = {
  renderComponent: "home",
  restaurants: [],
  meals: [],
  renderFlag: "",
  orderPrice: 0,
  orders: [],
};

export function reducer(state = initialState, action) {
  // console.log({ state });
  if (action.type === actions.SET_COMPONENT) {
    return setRenderComponent(state, action.payload.renderComponent);
  } else if (action.type === actions.SET_RESTAURANTS) {
    return setRestaurants(state, action.payload.restaurants);
  } else if (action.type === actions.SET_ORDERS) {
    return setOrders(state, action.payload.orders);
  } else if (action.type === actions.SET_MEALS) {
    return setMeals(state, action.payload.meals);
  } else if (action.type === "renderFlag") {
    console.log("hre");
    return {
      ...state,
      renderFlag: action.payload.message,
    };
  } else {
    return state;
  }
}
