import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Box from "@mui/material/Box";
import Dummy from "./dummy";
import { useNavigate } from "react-router";

const ViewRestaurants = (props) => {
  // const selectRestaurants = useSelector((state) => state.restaurants);
  const dispatch = useDispatch();
  const [selectRestaurants, setSelectRestaurants] = useState([]);
  let navigate = useNavigate();

  const dispatchRestaurants = (res) => {
    dispatch({
      type: "setRestaurants",
      payload: { restaurants: res.data.restaurants },
    });
  };

  useEffect(() => {
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios.get("/restaurants/", config).then((res) => {
      dispatchRestaurants(res);
      setSelectRestaurants(res.data.restaurants);
    });
  }, []);

  const renderList = () => {
    if (selectRestaurants.length > 0 && selectRestaurants !== "not found") {
      return (
        <div>
          {selectRestaurants.map((item) => (
            <Box
              key={item._id}
              onClick={() => {
                localStorage.setItem("restID", item._id);
                dispatch({
                  type: "setRenderComponent",
                  payload: { renderComponent: "view restaurant details" },
                });
                navigate("/view-meals");
              }}
              component="span"
              sx={{
                display: "block",
                width: "fit-content",
                cursor: "pointer",
                p: 1,
                m: 1,
                bgcolor: (theme) =>
                  theme.palette.mode === "dark" ? "#101010" : "#fff",
                color: (theme) =>
                  theme.palette.mode === "dark" ? "grey.300" : "grey.800",
                border: "1px solid",
                borderColor: (theme) =>
                  theme.palette.mode === "dark" ? "grey.800" : "grey.300",
                borderRadius: 2,
                fontSize: "0.875rem",
                fontWeight: "700",
              }}
            >
              {item.name}
            </Box>
          ))}
        </div>
      );
    } else {
      return (
        <div>
          <h3 style={{ color: "black" }}>No Restaurants</h3>
        </div>
      );
    }
  };
  return (
    <Dummy>
      <div style={{ color: "black", marginLeft: "20vw" }}>
        <h3>View Restaurant</h3>
        {renderList()}
      </div>
    </Dummy>
  );
};

export default ViewRestaurants;
