import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Box } from "@mui/material";
import { RemoveCircleOutline } from "@mui/icons-material";
import Parent from "./parent";
import { useNavigate } from "react-router-dom";

const ViewRestaurant = (props) => {
  const [selectRestaurants, setselectRestaurants] = useState([]);
  const dispatch = useDispatch();
  let navigate = useNavigate();

  const dispatchRestaurants = (res) => {
    dispatch({
      type: "setRestaurants",
      payload: { restaurants: res.data.restaurants },
    });
  };

  useEffect(() => {
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios
      .get("/restaurants/" + localStorage.getItem("ID"), config)
      .then((res) => {
        dispatchRestaurants(res);
        setselectRestaurants(res.data.restaurants);
      });
  }, []);

  const onDelete = (itemID) => {
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios.delete("/restaurants/" + itemID, config).then((res) => {
      axios.delete("/meals/restaurants/" + itemID, config).then((res) => {
        axios
          .get("/restaurants/" + localStorage.getItem("ID"), config)
          .then((res) => {
            dispatchRestaurants(res);
            setselectRestaurants(res.data.restaurants);
          });
      });
    });
  };

  const renderList = () => {
    if (selectRestaurants.length > 0 && selectRestaurants !== "not found") {
      return (
        <div>
          {selectRestaurants.map((item) => (
            <div key={item._id} style={{ display: "flex" }}>
              <Box
                key={item._id}
                onClick={() => {
                  localStorage.setItem("restID", item._id);
                  dispatch({
                    type: "setRenderComponent",
                    payload: { renderComponent: "view restaurant details" },
                  });
                  navigate("/seller-home/restaurant-details");
                }}
                component="span"
                sx={{
                  display: "block",
                  width: "fit-content",
                  cursor: "pointer",
                  p: 1,
                  m: 1,
                  bgcolor: (theme) =>
                    theme.palette.mode === "dark" ? "#101010" : "#fff",
                  color: (theme) =>
                    theme.palette.mode === "dark" ? "grey.300" : "grey.800",
                  border: "1px solid",
                  borderColor: (theme) =>
                    theme.palette.mode === "dark" ? "grey.800" : "grey.300",
                  borderRadius: 2,
                  fontSize: "0.875rem",
                  fontWeight: "700",
                }}
              >
                {item.name}
              </Box>
              <RemoveCircleOutline
                onClick={() => {
                  onDelete(item._id);
                  // setTimeout(function () {
                  //   window.location.reload(1);
                  // }, 1000);
                }}
                style={{ marginTop: "15px", color: "red", cursor: "pointer" }}
              ></RemoveCircleOutline>
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <div>
          <h3>No Restaurants</h3>
        </div>
      );
    }
  };

  return (
    <Parent>
      <div style={{ color: "black", marginLeft: "20vw" }}>
        <h3>View Restaurant</h3>
        {renderList()}
      </div>
    </Parent>
  );
};

export default ViewRestaurant;
