import LoginComponent from "./components/login-signup";
import SellerHome from "./components/seller/home";
import CustomerHome from "./components/customer/home";
import { Routes, Route, Navigate } from "react-router-dom";
import ViewRestaurants from "./components/customer/view-restaurants";
import ViewSellerRest from "./components/seller/view-restaurants";
import ViewRestDetails from "./components/seller/restaurant-details";
import ViewSellerOrders from "./components/seller/view-orders";
import AddRestaurant from "./components/seller/add-restaurant";
import ViewMeals from "./components/customer/view-meals";
import ViewOrders from "./components/customer/view-orders";
import { Button } from "@mui/material";
import Error from "./components/errorComponent";

const getSellerAuth = () => {
  const token = localStorage.getItem("token");
  const type = localStorage.getItem("type");
  if (token && type === "seller") {
    return true;
  } else {
    return false;
  }
};

const getCustomerAuth = () => {
  const token = localStorage.getItem("token");
  const type = localStorage.getItem("type");
  if (token && type === "customer") {
    return true;
  } else {
    return false;
  }
};

function CustomerAuth({ children, redirectTo }) {
  let isAuthenticated = getCustomerAuth();
  return isAuthenticated ? children : <Navigate to={redirectTo} />;
}

const isLoggedIn = () => {
  const token = localStorage.getItem("token");
  const type = localStorage.getItem("type");
  if (token && type) {
    return true;
  } else {
    return false;
  }
};

function SellerAuth({ children, redirectTo }) {
  let isAuthenticated = getSellerAuth();
  return isAuthenticated ? children : <Navigate to={redirectTo} />;
}

function LoginAuth({ children, redirectTo }) {
  let isAuthenticated = isLoggedIn();
  if (isAuthenticated) {
    if (localStorage.getItem("type") === "customer") {
      return <Navigate to={"/customer-home"} />;
    } else if (localStorage.getItem("type") === "seller") {
      return <Navigate to={"/seller-home"} />;
    }
  }
  return children;
}

const redirectTo = (url) => {
  if (localStorage.getItem("token")) {
    return url;
  } else {
    return "/";
  }
};

function App() {
  return (
    <>
      <div>
        <Routes>
          <Route path="view-restaurants" element={<ViewRestaurants />} />
          <Route path="view-orders" element={<ViewOrders />} />
          <Route path="view-meals" element={<ViewMeals />} />
          <Route path="seller-home/orders" element={<ViewSellerOrders />} />
          <Route
            path="seller-home/restaurant-details"
            element={<ViewRestDetails />}
          />
          <Route path="seller-home/restaurants" element={<ViewSellerRest />} />
          <Route
            path="seller-home/add-restaurant"
            element={<AddRestaurant />}
          />

          <Route
            path="/"
            element={
              <LoginAuth>
                <LoginComponent />
              </LoginAuth>
            }
          />
          <Route
            path="/seller-home"
            element={
              <SellerAuth redirectTo={redirectTo("/customer-home")}>
                <SellerHome />
              </SellerAuth>
            }
          />
          <Route
            path="/customer-home"
            element={
              <CustomerAuth redirectTo={redirectTo("/seller-home")}>
                <CustomerHome />
              </CustomerAuth>
            }
          />
          {/* <Route path="*" element={<Navigate to={"/"} />} /> */}
          <Route
            path="*"
            element={
              <Error
                message="Invalid Route"
                component={
                  <div>
                    <Button
                      onClick={() => {
                        window.location.href = "/";
                      }}
                    >
                      Redirect to Home
                    </Button>
                  </div>
                }
              ></Error>
            }
          />
        </Routes>
      </div>
    </>
  );
}

export default App;
