import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Button, Modal, Box, Typography, TextField } from "@mui/material";
import Cart from "./cart";
import axios from "axios";
import Dummy from "./dummy";

const ViewRestaurantDetails = () => {
  const [meals, setMeals] = useState([]);
  const selectRestaurants = useSelector((state) => state.restaurants);
  const dispatch = useDispatch();
  const [orderMeals, setOrderMeals] = useState([]);

  const renderDetails = () => {
    renderMeals();
    const id = localStorage.getItem("restID");
    const temp = selectRestaurants.filter((item) => {
      return item._id === id;
    })[0];

    return (
      <div>
        <div style={{ marginLeft: "1pc" }}>
          <h3 style={{ display: "inline", marginRight: "2pc" }}>{temp.name}</h3>
          <p style={{ marginLeft: "0pc" }}>{temp.description}</p>
        </div>
      </div>
    );
  };

  const onMealClick = (item) => {
    setOrderMeals([item, ...orderMeals]);
  };

  const renderMeals = () => {
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios
      .get("/meals/" + localStorage.getItem("restID"), config)
      .then((res) => {
        setMeals(res.data.meals);
      });
    return (
      <div>
        {meals.map((item) => (
          <Box
            key={item._id}
            component="span"
            onClick={() => {
              onMealClick(item);
              dispatch({
                type: "setMeals",
                payload: { meals: orderMeals },
              });
            }}
            sx={{
              cursor: "pointer",
              p: 1,
              m: 1,
              bgcolor: (theme) =>
                theme.palette.mode === "dark" ? "#101010" : "#fff",
              color: (theme) =>
                theme.palette.mode === "dark" ? "grey.300" : "grey.800",
              border: "1px solid",
              borderColor: (theme) =>
                theme.palette.mode === "dark" ? "grey.800" : "grey.300",
              borderRadius: 2,
              fontSize: "0.875rem",
              fontWeight: "700",
            }}
          >
            {item.name} (Rs. {item.price})
          </Box>
        ))}
      </div>
    );
  };

  return (
    <Dummy>
      <div style={{ color: "black", marginLeft: "20vw" }}>
        <h2>Restaurant Details</h2>
        {renderDetails()}
        {renderMeals()}
        <div style={{ position: "absolute", left: "60pc", top: "5pc" }}>
          <Cart></Cart>
        </div>
      </div>
    </Dummy>
  );
};

export default ViewRestaurantDetails;
