import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  Box,
  Button,
  Typography,
  Modal,
  InputLabel,
  MenuItem,
  Select,
  FormControl,
} from "@mui/material";
import Parent from "./parent";

const ViewOrders = (props) => {
  const dispatch = useDispatch();
  const [selectOrders, setselectOrders] = useState([]);
  const [orderFlag, setOrderFlag] = useState(true);

  const dispatchOrders = (res) => {
    dispatch({
      type: "setOrders",
      payload: { orders: res.data.orders },
    });
  };

  const renderOptions = () => {
    if (status === "Placed") {
      return (
        <Select
          disabled={flag}
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={status}
          label="Status"
          onChange={handleChange}
        >
          <MenuItem value={"Processing"}>Processing</MenuItem>
          <MenuItem value={"In Route"}>In Route</MenuItem>
          <MenuItem value={"Delivered"}>Delivered</MenuItem>
        </Select>
      );
    } else if (status === "Processing") {
      return (
        <Select
          disabled={flag}
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={status}
          label="Status"
          onChange={handleChange}
        >
          <MenuItem value={"Processing"}>Processing</MenuItem>
          <MenuItem value={"In Route"}>In Route</MenuItem>
          <MenuItem value={"Delivered"}>Delivered</MenuItem>
        </Select>
      );
    } else if (status === "In Route") {
      return (
        <Select
          disabled={flag}
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={status}
          label="Status"
          onChange={handleChange}
        >
          <MenuItem value={"In Route"}>In Route</MenuItem>
          <MenuItem value={"Delivered"}>Delivered</MenuItem>
        </Select>
      );
    } else if (status === "Delivered") {
      return (
        <Select
          disabled={flag}
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={status}
          label="Status"
          onChange={handleChange}
        >
          <MenuItem value={"Delivered"}>Delivered</MenuItem>
        </Select>
      );
    } else if (status === "Received") {
      return (
        <Select
          disabled={flag}
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={status}
          label="Status"
          onChange={handleChange}
        >
          {/* <MenuItem value={"Delivered"}>Delivered</MenuItem> */}
        </Select>
      );
    }
  };

  useEffect(() => {
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios
      .get("/order/seller-orders/" + localStorage.getItem("ID"), config)
      .then((res) => {
        dispatchOrders(res);
        setselectOrders(res.data.orders);
      });
  }, [orderFlag]);

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 300,
    bgcolor: "background.paper",
    border: "2px solid #000",
    borderRadius: "1pc",
    boxShadow: 20,
    p: 4,
  };

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [item, setItem] = useState("");
  const [status, setStatus] = useState("");
  const [flag, setFlag] = useState(false);

  const handleChange = (event) => {
    setStatus(event.target.value);
  };

  const orderDetails = () => {
    return (
      <div>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <h2>Price : {item.price}</h2>
            <h2>Status : {status.charAt(0).toUpperCase() + status.slice(1)}</h2>
            <Box sx={{ minWidth: 120 }}>
              <FormControl fullWidth variant="standard">
                <InputLabel id="demo-simple-select-label">Status</InputLabel>
                {renderOptions()}
              </FormControl>
              <Button
                style={{ marginTop: "1pc", marginLeft: "4pc" }}
                variant="contained"
                onClick={() => {
                  setOrderFlag(!orderFlag);
                  const config = {
                    headers: {
                      "x-access-token": localStorage.getItem("token"),
                      type: localStorage.getItem("type"),
                    },
                  };
                  handleClose();
                  axios.patch(
                    "/order/update-order/" + item._id,
                    {
                      sellerID: item.sellerID,
                      userID: item.userID,
                      restID: item.restID,
                      status: status,
                      meals: item.meals,
                      price: item.price,
                    },
                    config
                  );
                }}
              >
                Update
              </Button>
            </Box>
          </Box>
        </Modal>
      </div>
    );
  };

  const renderList = () => {
    if (selectOrders && selectOrders.length > 0) {
      return (
        <div>
          {selectOrders.map((item) => (
            <div key={item._id} style={{ display: "flex" }}>
              <Box
                onClick={() => {}}
                component="span"
                sx={{
                  display: "block",
                  width: "fit-content",
                  cursor: "pointer",
                  p: 1,
                  m: 1,
                  bgcolor: (theme) =>
                    theme.palette.mode === "dark" ? "#101010" : "#fff",
                  color: (theme) =>
                    theme.palette.mode === "dark" ? "grey.300" : "grey.800",
                  border: "1px solid",
                  borderColor: (theme) =>
                    theme.palette.mode === "dark" ? "grey.800" : "grey.300",
                  borderRadius: 2,
                  fontSize: "0.875rem",
                  fontWeight: "700",
                }}
              >
                Order # {item._id}
              </Box>
              <Button
                onClick={() => {
                  const disableList = ["Cancelled", "Received"];
                  if (disableList.includes(status)) {
                    if (status) setFlag(true);
                  } else {
                    // if ()
                    setFlag(false);
                  }
                  setItem(item);
                  setStatus(item.status);
                  // console.log(status);
                  const config = {
                    headers: {
                      "x-access-token": localStorage.getItem("token"),
                      type: localStorage.getItem("type"),
                    },
                  };
                  axios
                    .get("/order/order-details/" + item._id, config)
                    .then((res) => {});
                  handleOpen();
                }}
                style={{ display: "inline" }}
              >
                View
              </Button>
              {orderDetails()}
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <div>
          <h3>No Orders</h3>
        </div>
      );
    }
  };

  return (
    <Parent>
      <div style={{ color: "black", marginLeft: "20vw" }}>
        <h3>View Orders</h3>
        {renderList()}
      </div>
    </Parent>
  );
};

export default ViewOrders;
