import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { RemoveCircleOutline } from "@mui/icons-material";
import { Button, Modal, Box, Typography, TextField } from "@mui/material";
import axios from "axios";
import Parent from "./parent";

const RestaurantDetails = () => {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 250,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const onDelete = (itemID) => {
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios.delete("/meals/" + itemID, config).then((res) => {});
    setName("");
    setDescription("");
    setPrice(0);
  };

  const onAdd = () => {
    const id = localStorage.getItem("restID");
    const tempRest = selectRestaurants.filter((item) => {
      return item._id === id;
    })[0];
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    const temp = {
      name: name,
      price: price,
      sellerID: tempRest.sellerID,
      description: description,
      restID: tempRest._id,
    };
    axios.post("/meals", temp, config);
  };

  const [name, setName] = useState("");
  const [price, setPrice] = useState(0);
  const [meals, setMeals] = useState([]);
  const [description, setDescription] = useState("");
  const selectRestaurants = useSelector((state) => state.restaurants);
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const renderAddMeal = () => {
    return (
      <div>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <div>
              <TextField
                onChange={(e) => {
                  setName(e.target.value);
                }}
                label="Name"
                variant="standard"
              />
            </div>
            <div>
              <TextField
                onChange={(e) => {
                  setDescription(e.target.value);
                }}
                label="Description"
                variant="standard"
              />
            </div>
            <div>
              <TextField
                onChange={(e) => {
                  setPrice(e.target.value);
                }}
                label="Price"
                variant="standard"
              />
            </div>
            <div>
              <Button
                style={{ marginTop: "1pc", marginLeft: "3.5pc" }}
                onClick={() => {
                  onAdd();
                  setName("");
                  setPrice(0);
                  setDescription("");
                  handleClose();
                }}
                variant="contained"
              >
                Add
              </Button>
            </div>
          </Box>
        </Modal>
      </div>
    );
  };

  const renderDetails = () => {
    renderMeals();
    const id = localStorage.getItem("restID");
    const temp = selectRestaurants.filter((item) => {
      return item._id === id;
    })[0];

    return (
      <div>
        <div style={{ marginLeft: "1pc" }}>
          <h3 style={{ display: "inline", marginRight: "2pc" }}>{temp.name}</h3>
          <Button
            variant="contained"
            onClick={() => {
              handleOpen();
            }}
          >
            Add Meal
          </Button>
          {renderAddMeal()}
          <p style={{ marginLeft: "0pc" }}>{temp.description}</p>
        </div>
      </div>
    );
  };

  const renderMeals = () => {
    const config = {
      headers: {
        "x-access-token": localStorage.getItem("token"),
        type: localStorage.getItem("type"),
      },
    };
    axios
      .get("/meals/" + localStorage.getItem("restID"), config)
      .then((res) => {
        setMeals(res.data.meals);
      });
    return (
      <div>
        {meals.map((item) => (
          <div key={item._id} style={{ display: "flex" }}>
            <Box
              key={item._id}
              component="span"
              sx={{
                cursor: "pointer",
                p: 1,
                m: 1,
                bgcolor: (theme) =>
                  theme.palette.mode === "dark" ? "#101010" : "#fff",
                color: (theme) =>
                  theme.palette.mode === "dark" ? "grey.300" : "grey.800",
                border: "1px solid",
                borderColor: (theme) =>
                  theme.palette.mode === "dark" ? "grey.800" : "grey.300",
                borderRadius: 2,
                fontSize: "0.875rem",
                fontWeight: "700",
              }}
            >
              {item.name} (Rs. {item.price})
            </Box>
            <RemoveCircleOutline
              onClick={() => {
                onDelete(item._id);
              }}
              style={{ marginTop: "15px", color: "red", cursor: "pointer" }}
            ></RemoveCircleOutline>
          </div>
        ))}
      </div>
    );
  };

  return (
    <Parent>
      <div style={{ color: "black", marginLeft: "20vw" }}>
        <h2>Restaurant Details</h2>
        {renderDetails()}
        {renderMeals()}
      </div>
    </Parent>
  );
};

export default RestaurantDetails;
