import * as React from "react";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  HomeRounded,
  LogoutRounded,
  AddBusinessRounded,
  RestaurantRounded,
  DeliveryDiningRounded,
} from "@mui/icons-material";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import AppBar from "@mui/material/AppBar";
import CssBaseline from "@mui/material/CssBaseline";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import { useNavigate } from "react-router-dom";

const drawerWidth = 240;

export default function SellerSidebar() {
  let navigate = useNavigate();
  const dispatch = useDispatch();

  const dispatchComponentName = (name) => {
    dispatch({
      type: "setRenderComponent",
      payload: { renderComponent: name },
    });
  };

  // useEffect = () => {
  //   setTitle(localStorage.getItem("name"));
  // };

  useEffect(() => {
    setTitle(localStorage.getItem("name"));
  }, []);

  const [title, setTitle] = useState(localStorage.getItem("name"));
  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}
      >
        <Toolbar>
          <Typography variant="h6" noWrap component="div">
            Hello {title.charAt(0).toUpperCase() + title.slice(1)}
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          [`& .MuiDrawer-paper`]: {
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
      >
        <Toolbar />
        <Box sx={{ overflow: "auto" }}>
          <List>
            <ListItem
              button
              onClick={() => {
                navigate("/");
                dispatchComponentName("home");
              }}
            >
              <ListItemIcon>
                <HomeRounded></HomeRounded>
              </ListItemIcon>
              <ListItemText primary="Home" />
            </ListItem>
            <ListItem
              button
              onClick={() => {
                navigate("/seller-home/restaurants");
                dispatchComponentName("view restaurants");
              }}
            >
              <ListItemIcon>
                <RestaurantRounded></RestaurantRounded>
              </ListItemIcon>
              <ListItemText primary="View Restaurants" />
            </ListItem>
            <ListItem
              button
              onClick={() => {
                navigate("/seller-home/add-restaurant");
                dispatchComponentName("add restaurants");
              }}
            >
              <ListItemIcon>
                <AddBusinessRounded></AddBusinessRounded>
              </ListItemIcon>
              <ListItemText primary="Add Restaurant" />
            </ListItem>

            <ListItem
              button
              onClick={() => {
                navigate("/seller-home/orders");
                dispatchComponentName("view orders");
              }}
            >
              <ListItemIcon>
                <DeliveryDiningRounded></DeliveryDiningRounded>
              </ListItemIcon>
              <ListItemText primary="View Orders" />
            </ListItem>
          </List>
          <Divider />
          <List style={{ background: "#cce4f0", borderRadius: "10px" }}>
            <ListItem
              button
              onClick={() => {
                setTimeout(() => {
                  localStorage.clear();
                  navigate("/");
                }, 1000);
                dispatchComponentName("logout");
              }}
            >
              <ListItemIcon>
                <LogoutRounded></LogoutRounded>
              </ListItemIcon>
              <ListItemText style={{ paddingLeft: "13px" }} primary="Log Out" />
            </ListItem>
          </List>
        </Box>
      </Drawer>
      <Box component="main" sx={{ flexGrow: 1, p: 0 }}>
        <Toolbar />
      </Box>
    </Box>
  );
}
